import { Component, OnInit, Injector, HostListener } from '@angular/core';

import { VehicleService } from '../../../../api/services/vehicle.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-parking-management-settings',
  templateUrl: './parking-management-settings.component.html',
  styleUrls: ['./parking-management-settings.component.scss']
})
export class ParkingManagementSettingsComponent implements OnInit {

  
  isVehicleDataLoaded:boolean = false;
  vehicleCategoryData:any;

  isSlotMainDataLoaded:boolean = false;
  slotMainCategoryData:any;

  isSlotDataLoaded:boolean = false;
  slotCategoryData:any;

  constructor(
  	private injector: Injector,
    private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
  }


  ngOnInit() {

    //get vehcile type
    this.lookupService.getLookupValueByLookupTypeId(2).subscribe((res:any) => {

      this.isVehicleDataLoaded = true;

      this.vehicleCategoryData = res.filter(item => {
          return item.isActive;
      });

    });

    //get main slot type
    this.lookupService.getLookupValueByLookupTypeId(71).subscribe((res:any) => {

      this.isSlotMainDataLoaded = true;

      this.slotMainCategoryData = res.filter(item => {
          return item.isActive;
      });

    });


    //get slot type
    this.lookupService.getLookupValueByLookupTypeId(18).subscribe((res:any) => {

      this.isSlotDataLoaded = true;

      this.slotCategoryData = res.filter(item => {
          return item.isActive;
      });

    });

  }

}
